export default {
	data() {
		return {
			productList: [],
			totalPrice: '',
			show: false,
			radio: '1',
			name: '',
			avatar: ''
		}
	},
	methods: {
		// 结算弹窗
		popup() {
			this.show = true;
		},
		onClose() {
			this.show = false;
		},
		onClick(e) {
			this.radio = e.currentTarget.dataset.name
		},
		onGotUserInfo: function() {
			var that = this
			// wx登录
			wx.login({
				success(res) {
					if (res.code) {
						//发起网络请求
						var code = res.code
						// 获取微信用户信息
						wx.getUserInfo({
							success: function(res) {
								that.name = res.userInfo.nickName
								that.avatar = res.userInfo.avatarUrl
							},
							fail: res => {
								// 获取失败的去引导用户授权 
							}
						})

					} else {
						
					}
				}
			})
		},
		location(){
			wx.navigateTo({
				url:"../location/location"
			})
		}
	},
	onShow() {
		var that = this;
		uni.getStorage({
			key: 'cart',
			success(e) {
				var arr = JSON.parse(e.data)
				var list = []
				var totalPrice = 0;
				for (var i = 0; i < arr.length; i++) {
					if (arr[i].check) {
						list.push(arr[i])
						totalPrice += arr[i].allPrice
					}
				}
				that.productList = list
				that.totalPrice = totalPrice
			}
		})
	}
}
